<template>
  <div class="foot" v-show="lists.length">
    <ul class="nav">
      <!-- <li><router-link to="/home">首页</router-link></li>
      <li><router-link to="/news">新闻</router-link></li>
      <li><router-link to="/active">活动</router-link></li>
      <li><router-link to="/me">我</router-link></li> -->
      <li v-for="list in lists"><router-link :to="list.path">{{ list.title }}</router-link></li>
    </ul>
  </div>
</template>
<script>
  export default{
    data () {
      return {
        lists: [
          {
            'path': '/home',
            'title': '首页'
          },
          {
            'path': '/news',
            'title': '新闻'
          },
          {
            'path': '/active',
            'title': '活动'
          },
          {
            'path': '/me',
            'title': '我'
          },
          {
            'path': '/news',
            'title': 'So'
          }
        ]
      }
    }
  }
</script>
<style>
  .foot{
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 60px;
    background-color: rgba(0, 0, 0, 1);
    color: #fff;
    z-index: 99;
  }
  .nav{
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .nav li{
    flex: 1;
    height: 100%;
    flex-direction: column;
    vertical-align: middle;
  }
  .nav li a{
    text-decoration: none;
    display: block;
    height: 60px;
    line-height: 60px;
  }
</style>



